body{
    background-color: rgb(113, 207, 101);
}

.holder {    
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.bigBox {
    border: 2px gray solid;
    border-radius: 10px;
    background-image: linear-gradient(rgb(76, 143, 45), rgb(137, 245, 142));
}

.bundleBox {
    
    display: flex;
    flex-direction: row;
}

.mynd {
    width: 30px;
    height: 30px;
}

.myndReward {
    height: 30px;
}

.box {
    border: 1px gray solid;
    vertical-align: middle;
    width: 150px;
    height: 60px;
    border-radius: 10px;
    background-image: linear-gradient(rgb(158, 180, 123), rgb(222, 255, 196));
}
.box:hover {
    cursor: pointer;
  }

  .bundbox {
    background-image: linear-gradient(rgb(160, 157, 6), rgb(108, 255, 79));
    border: 1px gray solid;
    vertical-align: middle;
    width: 150px;
    height: 60px;
    border-radius: 10px;
}

.clicked {
    background-image: linear-gradient(rgb(11, 160, 6), rgb(108, 255, 79));
}

.bundle {
    background-color: gray;
    font-weight: bold;
    border: black 1px solid;
}

.hidden{
    position: absolute;
    white-space: nowrap;
    display: block;
    background: #ffffcc;
    border: 1px solid black;
    padding: 5px;
    z-index: 1000;
    color: black;
}

.resetButton{
    background-image: linear-gradient(rgb(216, 52, 11), rgb(255, 208, 79));
    width: 150px;
    display: flex;
    justify-content: center;
    height: 60px;
    border-radius: 15px;
    font-size: large;
}

.clickedBundle {
    background-image: linear-gradient(rgb(108, 255, 79),rgb(11, 160, 6));
}
